.type {
  margin-top: 28rem;
  margin-bottom: 20rem;
  padding-left: 5rem;
  padding-right: 5rem;

  @media (max-width: $tablet-width) {
    margin-top: 10rem;
    margin-bottom: 12rem;


  }


  @media (max-width: $mobile-width) {
    padding: 0;
    margin-top: 12.5rem;
    margin-bottom: 11rem;
  }
}
.type__title {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  text-align: center;
  margin-bottom: 6rem;

  @media (max-width: $tablet-width) {
    font-size: 24px;
    line-height: 41px;
    margin-bottom: 3rem;


  }
}

.type__photos-wrapper {
  display: flex;
  justify-content: center;
  position: relative;
  margin: 0 auto;
  gap: 5rem;

  @media (max-width: $tablet-width) {
    flex-wrap: wrap;
    justify-content: center;
    gap: 5rem;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    margin: 0 auto;
    align-items: center
  }

}

.type__photo-wrapper {
  width: 342px;
  height: 503px;

  @media (max-width: $tablet-width) {
    width: 270px;
    height: 300px;
  }
}

.type__photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0 0;
  border-radius: 10px;
}

.type__photo-wrapper--left {
  position: relative;

    &::before {
      content: "";
      position: absolute;
      left: -6rem;
      bottom: 33%;
      width: 18rem;
      height: 18rem;
      border: 27px solid #0AD9C6;
      border-radius: 50%;
      z-index: 1;

      @media (max-width: $tablet-width) {
        width: 127px;
        height: 127px;
        border: 18px solid #0AD9C6;
        left: -4rem;
        bottom: -3rem;
        z-index: -1;
      }

      @media (max-width: $mobile-width) {
      left: -2.5rem;
      }
  }
}


.type__photo-wrapper--center {
  margin-top: 3rem;
  position: relative;
  width: 342px;
  height: 566px;

  @media (max-width: $tablet-width) {
    margin-top: 0;
    margin-bottom: 6rem;
    width: 270px;
    height: 300px;
  }

  @media (max-width: $mobile-width) {
    margin-top: 5rem;
    margin-bottom: 4rem;
  }

  &::before {
    content: "";
    position: absolute;
//    right: 90%;
    left: -6%;
    top: -3%;
    width: 5rem;
    height: 5rem;
    border: 8px solid #0AD9C6;

    @media (max-width: $tablet-width) {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
//    right: 90%;
    right: -7%;
    bottom: -5%;
    width: 5rem;
    height: 5rem;
    border: 8px solid #0AD9C6;

    @media (max-width: $tablet-width) {
      display: none;
    }
  }
}

.type__photo-wrapper--right {
  position: relative;

    &::after {
      content: "";
      position: absolute;
      right: -3.5rem;
      top: -8%;
      width: 9.5rem;
      height: 9.5rem;
      border: 17px solid #0AD9C6;
      border-radius: 50%;
      z-index: 1;

      @media (max-width: $tablet-width) {
        width: 68px;
        height: 68px;
        border: 13px solid #0AD9C6;
        right: -2.5rem;
        top: -2rem;
  }
      @media (max-width: $mobile-width) {
        right: -1rem;
      }

  }
}

.type__caption {
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  letter-spacing: -0.5px;
  position: relative;

  @media (max-width: $tablet-width) {

  }

  @media (max-width: $mobile-width) {

  }
}


